html{
  scroll-snap-type: y mandatory;
}

.App{
  background-color: black;
  height: 100vh;
  display: grid;
  place-items: center;
}

.app__video{
  height: 100vh;
  max-height: 800px;
  width: 80%;
  max-width: 500px;
  border: 1px solid black;
  border-radius: 20px;
  overflow: scroll;
  scroll-snap-type: y mandatory;
  position: relative;
}

/*esconde o scrollbar do crhome, safari e opera*/
.app__video::-webkit-scrollbar{
  display: none;
}

/*esconde o scrollbar do internet explore, edge e firefox*/
.app__video{
  -ms-overflow-style: none;
  scrollbar-width: none;
}

@media only screen and ( max-width: 440px ){
  .app__video{
    width: 100%;
    height: 100vh;
  }
}